    }
  },
  mounted(){
    this.timer = setInterval(()=>{
      this.swiper();
    }, 3000)
  },
  beforeUnmount(){
    clearInterval(this.timer)
  },
  methods: {
    swiper(){
      this.index ++;
      this.index %= this.originalImgs.length;
    },
    clickBtn(){
      this.$router.push('/more_scenes')
    }
  }
